<template>
  <div>
    <div class="page-title">
      <h3>Общая статистика отрядов</h3>

      <button class="btn waves-effect waves-light btn-small" @click="refresh">
        <i class="material-icons">refresh</i>
      </button>
    </div>
    <div class="row">
      <p>Выберите период:</p>
      <select style="display: block" v-model="choosed">
        <option v-for="d of period" :key="d.id">{{ d.title }}</option>
      </select>
      <Loader v-if="loading" />
      <v-table v-else :data="n">
        <thead slot="head">
          <th>Место</th>
          <th>Отряд</th>
          <v-th sortKey="kills" defaultSort="desc">Убийств</v-th>
          <v-th sortKey="teamkills" defaultSort="desc">Тимкиллов</v-th>
          <v-th sortKey="score" defaultSort="desc">Счет</v-th>
        </thead>
        <tbody slot="body" slot-scope="{ displayData }">
          <tr v-for="(row, index) in displayData" :key="row.id">
            <td>{{ index + 1 }}</td>
            <td>{{ row.prefix }}</td>
            <td>{{ row.kills }}</td>
            <td>{{ row.teamkills }}</td>
            <td>{{ row.score }}</td>
          </tr>
        </tbody>
      </v-table>
    </div>
  </div>
</template>

<script>
export default {
  name: "Squads",
  data: () => ({
    n: [],
    loading: false,
    choosed: "За все время",
    period: [
      {
        id: 1,
        title: "За все время",
      },
      { id: 2, title: "За ротацию от 27.02.2022" },
    ],
  }),
  async mounted() {
    this.loading = true;
    await this.refresh();
    this.loading = false;
  },
  watch: {
    choosed: function () {
      this.refresh();
    },
  },
  methods: {
    async refresh() {
      if (this.choosed == this.period[0].title) {
        this.r();
      } else if ((this.choosed = this.period[1].title)) {
        this.rotationTable();
      }
    },
    async r() {
      this.loading = true;
      this.n = [];
      let r = await fetch("stats.json");
      r = await r.json();
      let id = 1;
      for (let key in r.squadStatistics) {
        r.squadStatistics[key].id = id;
        id++;
        this.n.push(r.squadStatistics[key]);
      }
      this.loading = false;
    },
    async rotationTable() {
      this.loading = true;
      this.n = [];
      let r = await fetch("stats.json");
      r = await r.json();
      let id = 1;
      for (let key in r.squadStatistics) {
        let newSquad = {
          prefix: "",
          players: [],
        };
        let nkills = 0,
          nteamkills = 0,
          ndeaths = 0,
          nscore = 0;
        let playerCount = 0;
        for (let key1 in r.squadStatistics[key].players) {
          for (let key2 in r.globalStatistics) {
            if (
              r.globalStatistics[key2].playerName ==
              r.squadStatistics[key].players[key1].playerName
            ) {
              let newPlayer = {};
              let newOne = [];
              let count = 0,
                kills = 0,
                teamkills = 0,
                deaths = 0;
              for (let i in r.globalStatistics[key2].byWeeks) {
                if (r.globalStatistics[key2].byWeeks[i].week >= "2022-10") {
                  newOne.push(r.globalStatistics[key2].byWeeks[i]);
                  let m = r.globalStatistics[key2].byWeeks[i];
                  count += m.totalPlayedGames;
                  kills += m.kills;
                  teamkills += m.teamkills;
                  deaths += m.deaths;
                }
              }
              if (count > 0) {
                newPlayer.id = id;
                id++;
                newPlayer.totalPlayedGames = count;
                newPlayer.kills = kills;
                newPlayer.teamkills = teamkills;
                newPlayer.deaths = deaths;
                newPlayer.rotationWeeks = newOne;
                newPlayer.playerName = r.globalStatistics[key2].playerName;
                newPlayer.lastSquadPrefix =
                  r.globalStatistics[key2].lastSquadPrefix;
                newPlayer.kdRatio = (kills / deaths).toFixed(2);
                newPlayer.totalScore = ((kills - teamkills) / count).toFixed(2);
                newSquad.players.push(newPlayer);
                playerCount++;
                nscore += Number(newPlayer.totalScore);
                nkills += kills;
                nteamkills += teamkills;
                ndeaths += deaths;
              }
            }
          }
        }
        newSquad.prefix = r.squadStatistics[key].prefix;
        newSquad.kills = nkills;
        newSquad.teamkills = nteamkills;
        newSquad.score = (nscore / playerCount).toFixed(2);
        newSquad.deaths = ndeaths;
        this.n.push(newSquad);
      }
      this.loading = false;
    },
  },
};
</script>
